.tableData tr{
   display: flex;
   justify-content: space-between;
   justify-content: space-around;
   align-content: center;

}

.tableData tr td{
    flex-basis: 15%;
    
}

.tableData{

   
    border:1px solid black;
    border-radius:10px;
    height:400px;
    overflow:auto;
    width:90vw;
    margin:10px auto;
    text-align: center;
}

.tableData tr:nth-of-type(1){
    font-weight: bold;
    position:sticky;
    top:0;
}


.tableData tr:nth-of-type(even){
    background-color: lightskyblue;
    
}
